.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.filter{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}
.filter-input{
	margin: 20px;
	padding: 5px 10px;
}
.filter-label{
	font-weight: bold;
}

.marker-label{
	display:none;
	font-size: 7px;
}
.marker-container:hover .marker-label{
 	display:block;
}
.carousel{
	padding: 1.5rem;
	margin-bottom: 1rem;
	background: lightgrey;
}
.carousel-item img{
	margin: 0 auto;
}
.carousel-indicators li{
	width: 10px;
	height:10px;
	-ms-flex-positive: 0;
	    flex-grow: 0;
	border-radius: 100%;
}
.section{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}


